@font-face {
    font-family: 'Museo Sans Cyrl 100';
    src: local('Museo Sans Cyrl 100'), url(./fonts/MuseoSansCyrl/MuseoSansCyrl-100.eot);
    src: local('Museo Sans Cyrl 100'), url(./fonts/MuseoSansCyrl/MuseoSansCyrl-100.eot?#iefix) format('mbedded-opentype');
    src: local('Museo Sans Cyrl 100'), url(./fonts/MuseoSansCyrl/MuseoSansCyrl-100.woff2) format('woff2');
    src: local('Museo Sans Cyrl 100'), url(./fonts/MuseoSansCyrl/MuseoSansCyrl-100.woff) format('woff');
    src: local('Museo Sans Cyrl 100'), url(./fonts/MuseoSansCyrl/MuseoSansCyrl-100.ttf) format('truetype');
    src: local('Museo Sans Cyrl 100'), url(./fonts/MuseoSansCyrl/MuseoSansCyrl-100.svg) format('svg');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

