.service_block {
  margin-right: 25px;
  margin-left: 25px;
  max-width: 206px;
}
@media screen and (max-width: 612px) {
  .service_block {
    margin-right: 10px;
    margin-left: 10px;
  }
}
.service_block .service_block {
  text-align: center;
}
.service_block .service_block.icon {
  margin-bottom: 30px;
  margin-right: auto;
  margin-left: auto;
  background-color: #dcce90;
  border-radius: 50%;
  padding: 15px;
  box-sizing: border-box;
  max-height: 85px;
  max-width: 85px;
  width: 100%;
  height: 100%;
  border: 2px #DCCE90 solid;
}
.service_block .service_block.icon img {
  height: 100%;
  width: 100%;
}
.service_block.line {
  margin-bottom: 15px;
}
.service_block h4 {
  font-family: "Museo Sans Cyrl", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
  color: #212121;
  max-width: 128px;
  margin: auto;
}
.service_block p {
  font-family: "Museo Sans Cyrl", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 145%;
  text-align: center;
  color: #212121;
}

